
.el-row {
    margin-bottom: 20px;
&:last-child {
      margin-bottom:10px;
}
}
.form-div {
    border-radius: 14px;
    padding: 0px 0;
    margin:0;
    width:1200px;
}
.el-col {
    border-radius: 14px;
    padding: 0px 0;
}
.request-info {
    background: #f2f3ee;
    font-size:.9em;
    margin-top:-1em;
}
.request-submit {
    -webkit-text-fill-color:rgb(155, 18, 45);
    font-size:.9em;
    margin-top:-.5em;
    font-weight: 600;
}
.bg-purple {
    background: #d3dce6;
}
.bg-purple-light {
    background: #e5e9f2;
}
.row-bg {
    padding: 0px 0;
    background-color: #f9fafc;
}
 
