.el-form {
    background: #f7f7f3;
    margin-top:0px;
    margin-right:10px;
    margin-left:0px;
    top: 1px;
    padding:10px;
    font-size:1em;
    width: 100%;
    border: 0px solid #666;
    text-align: left;
}
.el-item {
    left: 1px;
    top: 1px;
    font-size:10em;
    text-align: left;
}
.el-input__inner{
    padding-bottom: 2px;
    font-size:1.3em;
    margin-top:1px;
    font-weight: 300
}
.el-form-item__error{
    margin-top: -.7em;
}
.el-form-item__label{
    padding-bottom: 2px;
    font-size:.9em;
    margin-top:1px;
    font-weight: 500
}
.el-form-item-first{
    padding-bottom: 2px;
    margin-top:1em;
    margin-top: 10em;
}
.el-form-item-mini{
    background: rgb(194, 12, 12);
    padding-bottom: 2px;
    margin-top:1px;
}
.el-form-item{
    margin-right:10px;
    margin-bottom: -.2em;
    margin-top: -.7em;
    padding: 0 ;
}
.el-select-dropdown__item{
    margin-right:1px;
    margin-bottom:-.8em;;
    margin-top: 0;
    padding: 0 ;
    /* &:first-child {
        margin-top:100px;
    } */
}
